

import { ComponentPortal, Portal, TemplatePortal } from '@angular/cdk/portal';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Injector,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  SimpleChanges,
  TemplateRef,
  Type,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { startWith, takeUntil } from 'rxjs/operators';
import { TDSConfigService } from 'tds-ui/core/config';
import { TDSSafeAny } from 'tds-ui/shared/utility';

import { TDSEmptyCustomContent, TDSEmptySize, TDS_EMPTY_COMPONENT_NAME } from './config';

function getEmptySize(componentName: string): TDSEmptySize {
  switch (componentName) {
    case 'table':
    case 'list':
      return 'normal';
    case 'select':
    case 'tree-select':
    case 'cascader':
    case 'transfer':
      return 'small';
    default:
      return '';
  }
}

type TDSEmptyContentType = 'component' | 'template' | 'string';

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'tds-embed-empty',
  exportAs: 'tdsEmbedEmpty',
  template: `
    <ng-container *ngIf="!content && specificContent !== null" [ngSwitch]="size">
      <tds-empty *ngSwitchCase="'lg'" class="tds-empty-normal" [notFoundImage]="'simple'"></tds-empty>
      <tds-empty *ngSwitchCase="'md'" class="tds-empty-small" [notFoundImage]="'simple'"></tds-empty>
      <tds-empty *ngSwitchDefault></tds-empty>
    </ng-container>
    <ng-container *ngIf="content">
      <ng-template *ngIf="contentType !== 'string'" [cdkPortalOutlet]="contentPortal"></ng-template>
      <ng-container *ngIf="contentType === 'string'">
        {{ content }}
      </ng-container>
    </ng-container>
  `
})
export class TDSEmbedEmptyComponent implements OnChanges, OnInit, OnDestroy {
  @Input() componentName?: string;
  @Input() specificContent?: TDSEmptyCustomContent;

  content?: TDSEmptyCustomContent;
  contentType: TDSEmptyContentType = 'string';
  contentPortal?: Portal<TDSSafeAny>;
  size: TDSEmptySize = '';

  private destroy$ = new Subject<void>();

  constructor(
    private configService: TDSConfigService,
    private viewContainerRef: ViewContainerRef,
    private cdr: ChangeDetectorRef,
    private injector: Injector
  ) { }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.componentName) {
      this.size = getEmptySize(changes.componentName.currentValue);
    }

    if (changes.specificContent && !changes.specificContent.isFirstChange()) {
      this.content = changes.specificContent.currentValue;
      this.renderEmpty();
    }
  }

  ngOnInit(): void {
    this.subscribeDefaultEmptyContentChange();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  private renderEmpty(): void {
    const content = this.content;

    if (typeof content === 'string') {
      this.contentType = 'string';
    } else if (content instanceof TemplateRef) {
      const context = { $implicit: this.componentName } as TDSSafeAny;
      this.contentType = 'template';
      this.contentPortal = new TemplatePortal(content, this.viewContainerRef, context);
    } else if (content instanceof Type) {
      const injector = Injector.create({
        parent: this.injector,
        providers: [{ provide: TDS_EMPTY_COMPONENT_NAME, useValue: this.componentName }]
      });
      this.contentType = 'component';
      this.contentPortal = new ComponentPortal(content, this.viewContainerRef, injector);
    } else {
      this.contentType = 'string';
      this.contentPortal = undefined;
    }

    this.cdr.detectChanges();
  }

  private subscribeDefaultEmptyContentChange(): void {
    this.configService
      .getConfigChangeEventForComponent('empty')
      .pipe(startWith(true), takeUntil(this.destroy$))
      .subscribe(() => {
        this.content = this.specificContent || this.getUserDefaultEmptyContent();
        this.renderEmpty();
      });
  }

  private getUserDefaultEmptyContent(): Type<TDSSafeAny> | TemplateRef<string> | string | undefined {
    return (this.configService.getConfigForComponent('empty') || {}).TDSDefaultEmptyContent;
  }
}
