

import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  SimpleChanges,
  TemplateRef,
  ViewEncapsulation
} from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { TDSEmptyI18nInterface, TDSI18nService } from 'tds-ui/i18n';


const TDSEmptyDefaultImages = ['default', 'simple'] as const;
type TDSEmptyNotFoundImageType = typeof TDSEmptyDefaultImages[number] | null | string | TemplateRef<void>;

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'tds-empty',
  exportAs: 'tdsEmpty',
  template: `
    <div class="tds-empty-image">
      <ng-container *ngIf="!isImageBuildIn">
        <ng-container *tdsStringTemplateOutlet="notFoundImage">
          <img class="h-full" [src]="notFoundImage" [alt]="isContentString ? notFoundContent : 'empty'" />
        </ng-container>
      </ng-container>
      <tds-empty-default *ngIf="isImageBuildIn && notFoundImage !== 'simple'"></tds-empty-default>
      <tds-empty-simple *ngIf="isImageBuildIn && notFoundImage === 'simple'"></tds-empty-simple>
    </div>
    <p class="tds-empty-description text-neutral-1-900 text-title-1 font-semibold mt-3" *ngIf="notFoundContent !== null">
      <ng-container *tdsStringTemplateOutlet="notFoundContent">
        {{ isContentString ? notFoundContent : locale['description'] }}
      </ng-container>
    </p>
    <div class="tds-empty-footer mt-3" *ngIf="notFoundFooter">
      <ng-container *tdsStringTemplateOutlet="notFoundFooter">
        {{ notFoundFooter }}
      </ng-container>
    </div>
  `,
  host: {
    class: 'tds-empty  flex flex-col items-center'
  }
})
export class TDSEmptyComponent implements OnChanges, OnInit, OnDestroy {
  @Input() notFoundImage: TDSEmptyNotFoundImageType = 'default';
  @Input() notFoundContent?: string | TemplateRef<void> | null;
  @Input() notFoundFooter?: string | TemplateRef<void>;

  isContentString = false;
  isImageBuildIn = true;
  locale!: TDSEmptyI18nInterface;

  private readonly destroy$ = new Subject<void>();

  constructor(private i18n: TDSI18nService, private cdr: ChangeDetectorRef) {}

  ngOnChanges(changes: SimpleChanges): void {
    const { notFoundContent, notFoundImage } = changes;

    if (notFoundContent) {
      const content = notFoundContent.currentValue;
      this.isContentString = typeof content === 'string';
    }

    if (notFoundImage) {
      const image = notFoundImage.currentValue || 'default';
      this.isImageBuildIn = TDSEmptyDefaultImages.findIndex(i => i === image) > -1;
    }
  }

  ngOnInit(): void {
    this.i18n.localeChange.pipe(takeUntil(this.destroy$)).subscribe(() => {
      this.locale = this.i18n.getLocaleData('Empty');
      this.cdr.markForCheck();
    });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }
}
